.scop__footer {
    display: flex;
    color: white;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background: var(--color-footer);
}

.scop__footer-heading {
    width: 100%;
    text-align: center;
    margin-bottom: 3rem;
}

.scop__footer-heading h1 {
    font-family: var(--font-family);
    font-weight: 800;
    text-align: center;
    font-size: 62px;
    line-height: 75px;
}

.scop__footer-links {
    justify-content: space-between;
    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
    flex-direction: row;
    width: 100%;
    text-align: left;
}

.scop__footer-links div {
    width: 250px;
    margin: 1rem;
}

.scop__footer-links_logo {
    display: flex;
    flex-direction: column;
}

.scop__footer-links_logo img {
    width: 120px;
    height: 40px;
    margin-bottom: 1rem;
}

.scop__footer-links_logo p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
}

.scop__footer-links_div {
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.scop__footer-links_div h4 {
    font-family: var(--font-family);
    font-size: 14px;
    line-height: 16px;

    margin-bottom: 1rem;
}

.scop__footer-links_div p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
    margin: 0.5rem 0;
    cursor: pointer;
}

.scop__footer-copyright {
    width: 100%;
    margin-top: 2rem;
    text-align: center;
}

.scop__footer-copyright p {
    font-family: var(--font-family);
    font-size: 12px;
    line-height: 14px;
}

@media screen and (max-width: 850px) {
    .scop__footer-heading h1 {
        font-size: 44px;
        line-height: 50px;
    }
}

@media screen and (max-width: 550px) {
    .scop__footer-heading h1 {
        font-size: 34px;
        line-height: 42px;
    }

    .scop__footer-links div {
        margin: 1rem 0;
    }
}